import React, { useState } from "react";
import trash from "./trash.png";

import { SocialIcon } from "react-social-icons";

function ListItem(props) {
  return (
    <div className="list-items row jc-space-between">
      <span
        className={props.itemData.isDone ? "task-done" : ""}
        onClick={() => {
          props.DoneTask(props.index);
        }}
      >
        {props.itemData.description}
      </span>
      <img
        className="delete-icon"
        src={trash}
        alt="delete-icon"
        onClick={() => props.deleteTask(props.index)}
      />
    </div>
  );
}

function App() {
  const [taskInput, updateTask] = useState("");

  const [todoList, updateTodo] = useState([]);

  const AddTask = () => {
    todoList.push({ description: taskInput, isDone: false });
    updateTodo(todoList);
    updateTask("");
  };

  const inputKeyDown = (event) => {
    if (event.keyCode === 13) AddTask();
  };

  const deleteTask = (index) => {
    const modified = todoList.filter((item, i) => i !== index);
    updateTodo(modified);
  };

  const DoneTask = (index) => {
    const list = [...todoList];
    list[index].isDone = !list[index].isDone;
    updateTodo(list);
  };

  return (
    <div className="app-bg">
      <p className="head">To-Do List </p>
      <div className="task-container">
        <div>
          <input
            className="inputText"
            value={taskInput}
            onKeyDown={inputKeyDown}
            onChange={(event) => updateTask(event.target.value)}
          />
          <button className="add-button" onClick={AddTask}>
            Add
          </button>
        </div>
        {todoList?.length ? (
          todoList.map((todoObject, index) => (
            <ListItem
              index={index}
              itemData={todoObject}
              deleteTask={deleteTask}
              DoneTask={DoneTask}
            />
          ))
        ) : (
          <p className="no-task">Get started!</p>
        )}
      </div>

      <div className="footer">
        Vishwa Shah
        <br />
        <SocialIcon url="https://github.com/vishwa-187" />
        <SocialIcon url="https://www.linkedin.com/in/vishwa-shah-94a9001a8/" />
        <SocialIcon url="mailto:vishwashah180701@gmail.com" />
      </div>
    </div>
  );
}

export default App;
